import { includes } from 'lodash'

export type Actions =
  // views
  | 'VIEW_DASHBOARD_PAGE'
  // authentication
  | 'LOGIN'
  | 'LOGOUT'
  // tournaments
  | 'GET_TOURNAMENTS'
  | 'GET_TOURNAMENT_DETAIL'
  | 'CREATE_TOURNAMENTS'
  | 'GET_TOURNAMENT_CURRENT'
  | 'UPDATE_TOURNAMENT'
  // champions
  | 'GET_CHAMPIONS'
  | 'GET_CHAMPION_DETAIL'
  // error pages
  | 'VIEW_NOT_FOUND_PAGE'

export type Roles = 'super_admin' | 'admin' | 'user' | 'guest'

export const ROLE_IDS: { [key: string]: number } = {
  super_admin: 1,
  admin: 2,
  user: 3,
  guest: 4
}

const commonRoles: Array<Actions> = ['VIEW_NOT_FOUND_PAGE']

export const ROLES: { [key: string]: { id: number; can: Array<Actions> } } = {
  super_admin: {
    id: ROLE_IDS.super_admin,
    can: []
  },
  admin: {
    id: ROLE_IDS.admin,
    can: [
      // auth
      'LOGOUT',

      // views
      'VIEW_DASHBOARD_PAGE',

      // tournaments
      'CREATE_TOURNAMENTS',
      'GET_TOURNAMENTS',
      'GET_TOURNAMENT_CURRENT',
      'GET_TOURNAMENT_DETAIL',
      'UPDATE_TOURNAMENT',

      // champions
      'GET_CHAMPIONS',
      'GET_CHAMPION_DETAIL',

      // others
      ...commonRoles
    ]
  },
  user: {
    id: ROLE_IDS.user,
    can: [
      // auth
      'LOGOUT',

      // others
      ...commonRoles
    ]
  },
  guest: {
    id: ROLE_IDS.guest,
    can: [
      // auth
      'LOGIN',

      // others
      ...commonRoles
    ]
  }
}

type RoleParams = {
  role: string
  action: Actions | undefined
}

export const isSuperAdmin = (role: string) => {
  return ROLE_IDS[role] === ROLE_IDS.super_admin
}

export const roleCan = (params: RoleParams): boolean => {
  const { role, action } = params
  // check if full access
  if (isSuperAdmin(role)) return true

  const roleActions = ROLES[role]?.can
  return includes(roleActions, action)
}
