import * as React from 'react'
import {
  Box,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TablePagination,
  TableRow,
  TableSortLabel,
  Toolbar,
  Typography,
  Paper,
  Checkbox,
  IconButton,
  Tooltip,
  Link
} from '@mui/material'
import DeleteIcon from '@mui/icons-material/Delete'
import AddCircleOutlineIcon from '@mui/icons-material/AddCircleOutline'
import LaunchIcon from '@mui/icons-material/Launch'
import EditIcon from '@mui/icons-material/Edit'
import { map, omit } from 'lodash'

type Order = 'asc' | 'desc'

export const DataListTable = <T extends { index: number; key: string; link_detail?: string; link_edit?: string }>(props: {
  toolbar: {
    title: string
    remove?: {
      onDelete: (selected: readonly number[]) => void
    }
    create?: {
      onCreate: () => void
    }
  }
  rows: T[]
  heads: {
    id: keyof T
    label: string
    can_sort: boolean
  }[]
  page?: {
    current_page: number
    total_pages: number
    item_count: number
    total_count: number
  }
  filters: {
    onRowsPerPageChange: (rowsPerPage: number, orderBy: keyof T, order: Order) => void
    onPageChange: (page: number, rowsPerPage: number, orderBy: keyof T, order: Order) => void
    onFieldSort: (property: keyof T, order: Order, rowsPerPage: number) => void
  }
}) => {
  const { rows, heads, toolbar } = props
  const [order, setOrder] = React.useState<Order>('asc')
  const [orderBy, setOrderBy] = React.useState<keyof T>('index')
  const [selected, setSelected] = React.useState<readonly number[]>([])
  const [rowsPerPage, setRowsPerPage] = React.useState(props.page?.item_count || 30)

  const handleRequestSort = (property: keyof T) => (event: React.MouseEvent<unknown>) => {
    const isAsc = orderBy === property && order === 'asc'
    const newOrder = isAsc ? 'desc' : 'asc'
    setOrder(newOrder)
    setOrderBy(property)
    props.filters.onFieldSort(property, newOrder, rowsPerPage)
  }

  const handleSelectAllClick = (event: React.ChangeEvent<HTMLInputElement>) => {
    if (event.target.checked) {
      const newsSelected = rows.map(n => n.index)
      setSelected(newsSelected)
      return
    }
    setSelected([])
  }

  const handleSelected = (event: React.MouseEvent<unknown>, index: number) => {
    const selectedIndex = selected.indexOf(index)
    let newSelected: readonly number[] = []

    if (selectedIndex === -1) {
      newSelected = newSelected.concat(selected, index)
    } else if (selectedIndex === 0) {
      newSelected = newSelected.concat(selected.slice(1))
    } else if (selectedIndex === selected.length - 1) {
      newSelected = newSelected.concat(selected.slice(0, -1))
    } else if (selectedIndex > 0) {
      newSelected = newSelected.concat(selected.slice(0, selectedIndex), selected.slice(selectedIndex + 1))
    }

    setSelected(newSelected)
  }

  const handleChangePage = (event: unknown, newPage: number) => {
    props.filters.onPageChange(newPage, rowsPerPage, orderBy, order)
  }

  const handleChangeRowsPerPage = (event: React.ChangeEvent<HTMLInputElement>) => {
    setRowsPerPage(parseInt(event.target.value, 10))
    props.filters.onRowsPerPageChange(parseInt(event.target.value, 10), orderBy, order)
  }

  const isSelected = (index: number) => selected.indexOf(index) !== -1

  const numSelected = selected.length
  const rowCount = rows.length

  return (
    <Box sx={{ width: '100%' }}>
      <Paper sx={{ width: '100%', mb: 2 }}>
        <Toolbar
          sx={{
            pl: { sm: 2 },
            pr: { xs: 1, sm: 1 }
          }}
        >
          {numSelected > 0 ? (
            <Typography sx={{ flex: '1 1 100%' }} color="inherit" variant="subtitle1" component="div">
              {numSelected} selected
            </Typography>
          ) : (
            <Typography sx={{ flex: '1 1 100%' }} variant="h6" id="tableTitle" component="div">
              {toolbar.title}
            </Typography>
          )}
          {numSelected > 0 && toolbar.remove && (
            <Tooltip title="Delete">
              <IconButton
                onClick={() => {
                  toolbar.remove?.onDelete(selected)
                }}
              >
                <DeleteIcon color="primary" />
              </IconButton>
            </Tooltip>
          )}
          {numSelected <= 0 && toolbar.create && (
            <Tooltip title={`Add a new ${toolbar.title.toLowerCase()}`}>
              <IconButton onClick={toolbar.create?.onCreate}>
                <AddCircleOutlineIcon color="primary" />
              </IconButton>
            </Tooltip>
          )}
        </Toolbar>

        <TableContainer>
          <Table sx={{ minWidth: 750 }} size="medium">
            <TableHead>
              <TableRow sx={{ borderBottom: '1px solid rgba(81, 81, 81, 1)' }}>
                <TableCell padding="checkbox">
                  <Checkbox
                    color="primary"
                    indeterminate={numSelected > 0 && numSelected < rowCount}
                    checked={rowCount > 0 && numSelected === rowCount}
                    onChange={handleSelectAllClick}
                  />
                </TableCell>
                {map(heads, (headCell, key) => (
                  <TableCell key={key} align="center" padding="normal" sx={{ color: '#f6d34a' }}>
                    {headCell.can_sort && (
                      <TableSortLabel
                        active={orderBy === headCell.id}
                        direction={orderBy === headCell.id ? order : 'asc'}
                        onClick={handleRequestSort(headCell.id)}
                      >
                        {headCell.label}
                      </TableSortLabel>
                    )}
                    {!headCell.can_sort && headCell.label}
                  </TableCell>
                ))}
              </TableRow>
            </TableHead>

            <TableBody>
              {rows.map((row, index) => {
                const isItemSelected = isSelected(row.index)

                return (
                  <TableRow hover role="checkbox" tabIndex={-1} key={row.index} sx={{ cursor: 'pointer' }}>
                    <TableCell padding="checkbox">
                      <Checkbox color="primary" checked={isItemSelected} onClick={event => handleSelected(event, row.index)} />
                    </TableCell>
                    {map(omit(row, ['index', 'key', 'link_detail', 'link_edit']), (value, key) => {
                      return (
                        <TableCell align="center" key={key}>
                          {value}
                        </TableCell>
                      )
                    })}
                    {row.link_detail && (
                      <TableCell align="center" key={row.link_detail} colSpan={1}>
                        <Tooltip title="View detail">
                          <Link href={row.link_detail}>
                            <LaunchIcon />
                          </Link>
                        </Tooltip>
                      </TableCell>
                    )}
                    {row.link_edit && (
                      <TableCell align="center" key={row.link_edit} colSpan={1}>
                        <Tooltip title="Edit">
                          <Link href={row.link_edit}>
                            <EditIcon />
                          </Link>
                        </Tooltip>
                      </TableCell>
                    )}
                  </TableRow>
                )
              })}
              {rowCount === 0 && (
                <TableRow>
                  <TableCell align="center" colSpan={100}>
                    No Data
                  </TableCell>
                </TableRow>
              )}
            </TableBody>
          </Table>
        </TableContainer>
        <TablePagination
          rowsPerPageOptions={[30, 50, 100]}
          showFirstButton
          showLastButton
          component="div"
          count={props.page?.total_count || rows.length}
          rowsPerPage={rowsPerPage}
          page={props.page?.current_page || 0}
          onPageChange={handleChangePage}
          onRowsPerPageChange={handleChangeRowsPerPage}
        />
      </Paper>
    </Box>
  )
}
