import { getAuth } from '../utils/auth'
import { setAuth } from './actions/auth.actions'
import { LOGIN_FILLED } from './constants/auth.action.types'
import { SET_ACCOUNT, SET_PROVIDER } from './constants/wallet.action.types'

export const logger = (store: any) => (next: any) => (action: { type: string; payload: any }) => {
  console.log('dispatching', action)
  const result = next(action)
  console.log('next state', store.getState())
  return result
}

export const crashReporter = (store: any) => (next: any) => (action: { type: string; payload: any }) => {
  try {
    return next(action)
  } catch (err) {
    console.error('Caught an exception!', err)
    throw err
  }
}

export const authMiddleware = (store: any) => (next: any) => (action: { type: string; payload: any }) => {
  if (action.type === LOGIN_FILLED || action.type === SET_ACCOUNT || action.type === SET_PROVIDER) {
    return next(action)
  }
  const user = getAuth()
  console.log(user)
  if (user) {
    store.dispatch(setAuth(user))
  }
  return next(action)
}
