import { Contract, ethers } from 'ethers'
import { APP_ENVIRONMENTS } from '../configs'

const provider = new ethers.providers.JsonRpcProvider(APP_ENVIRONMENTS.ETHEREUM_PROVIDER)

export const connectTournamentContract = (): Contract => {
  return new Contract(APP_ENVIRONMENTS.TOURNAMENT_CONTRACT_ADDRESS, APP_ENVIRONMENTS.TOURNAMENT_ABI, provider)
}
