import React from 'react'
import { ChampionRequest } from '../../requests'
import { DataListTable } from '../../components/commons/DataTable'
import { find, map } from 'lodash'

import './styles/champion.scss'
import { IChampions } from '../../requests/champions/types'
import { convertAttributes } from '../../utils/champion'

const championRequest = new ChampionRequest()

type Order = 'asc' | 'desc'

interface Champion {
  index: number
  id: number
  key: string
  name: string
  character_class: string
  genotype: string
  bloodline: string
  link_detail?: string
}

interface HeadCell {
  id: keyof Champion
  label: string
  can_sort: boolean
}

// apply to props
const headCells: HeadCell[] = [
  {
    id: 'id',
    label: 'ID',
    can_sort: true
  },
  {
    id: 'name',
    label: 'Name',
    can_sort: true
  },
  {
    id: 'character_class',
    label: 'Character Class',
    can_sort: false
  },
  {
    id: 'genotype',
    label: 'Genotype',
    can_sort: false
  },
  {
    id: 'bloodline',
    label: 'Bloodline',
    can_sort: false
  }
]

export const ChampionsPage = (props: any) => {
  const [champions, setChampions] = React.useState<Champion[]>([])
  const [pageInfo, setPageInfo] = React.useState<
    | {
        current_page: number
        total_pages: number
        item_count: number
        total_count: number
      }
    | undefined
  >()

  const mapData = (data: IChampions) => {
    const rows = map(data.items, (el, idx) => {
      const attributesConverted = convertAttributes(el.attributes)
      const characterClass = String(find(attributesConverted, element => element.label === 'Character Class')?.trait)
      const genotype = String(find(attributesConverted, element => element.label === 'Genotype')?.trait)
      const bloodline = String(find(attributesConverted, element => element.label === 'Bloodline')?.trait)
      return {
        index: idx,
        key: el.key,
        id: el.id,
        name: el.name,
        character_class: characterClass,
        genotype,
        bloodline,
        link_detail: `champions/${el.key}/detail`
      }
    })
    setPageInfo({
      current_page: data.current_page,
      item_count: data.item_count,
      total_count: data.total_count,
      total_pages: data.total_pages
    })
    setChampions(rows)
  }

  const fetchData = async () => {
    try {
      const data = await championRequest.getChampions()
      mapData(data)
    } catch (err) {}
  }

  React.useEffect(() => {
    fetchData()
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [])

  const onPageSizeChange = async (size: number, orderBy: keyof Champion, order: Order) => {
    const sorts =
      orderBy !== 'index'
        ? {
            [`sort_${orderBy}`]: order
          }
        : {}
    try {
      const data = await championRequest.getChampions({}, sorts, {
        page_index: 0,
        page_size: size
      })
      mapData(data)
    } catch (err) {}
  }

  const onPageChange = async (page: number, size: number, orderBy: keyof Champion, order: Order) => {
    const sorts =
      orderBy !== 'index'
        ? {
            [`sort_${orderBy}`]: order
          }
        : {}
    try {
      const data = await championRequest.getChampions({}, sorts, {
        page_index: page,
        page_size: size
      })
      mapData(data)
    } catch (err) {}
  }

  const onFieldSort = async (property: keyof Champion, order: 'asc' | 'desc', size: number) => {
    try {
      const data = await championRequest.getChampions(
        {},
        {
          [`sort_${property}`]: order
        },
        {
          page_index: 0,
          page_size: size
        }
      )
      mapData(data)
    } catch (err) {}
  }

  return (
    <div className="wrapper-champions">
      <DataListTable
        toolbar={{ title: 'Champions' }}
        rows={champions}
        heads={headCells}
        page={pageInfo}
        filters={{
          onRowsPerPageChange: onPageSizeChange,
          onPageChange,
          onFieldSort
        }}
      />
    </div>
  )
}
