import { map } from 'lodash'
import React from 'react'
import { useRoutes } from 'react-router-dom'
import { roleCan, Roles } from '../configs'
import { PAGES } from './index'

type AppRoutesProps = {
  userRole: Roles
}

export const AppRoutes = (props: AppRoutesProps) => {
  const { userRole } = props

  const routes = map(PAGES, page => {
    return roleCan({ role: userRole, action: page.require_role_can })
      ? {
          path: page.path,
          element: <page.element />,
          children: page.children.map(child => {
            // if using children as nested route need add <OutLet /> to parent.
            return roleCan({ role: userRole, action: child.require_role_can })
              ? {
                  path: child.path,
                  element: <child.element />
                }
              : {}
          })
        }
      : {}
  })
  return useRoutes(routes)
}
