import { AuthRequest } from './auth/AuthRequest'
import { TournamentRequest } from './tournaments/TournamentsRequest'
import { ChampionRequest } from './champions/ChampionsRequest'

type RequestEnum = 'AuthRequest' | 'TournamentRequest' | 'ChampionRequest'

const RequestMap: { [key in RequestEnum]: any } = {
  AuthRequest,
  TournamentRequest,
  ChampionRequest
}

const instances: any = {}

export class RequestFactory {
  static getRequest<T>(className: RequestEnum): T {
    const RequestClass = RequestMap[className]
    if (!RequestClass) {
      throw new Error(`Invalid request class name: ${className}`)
    }

    let requestInstance = instances[className]
    if (!requestInstance) {
      requestInstance = new RequestClass()
      instances[className] = requestInstance
    }
    return requestInstance
  }
}

export * from './auth/AuthRequest'
export * from './tournaments/TournamentsRequest'
export * from './champions/ChampionsRequest'
