import { omit } from 'lodash'
import { APP_ENVIRONMENTS } from '../../configs'
import { LOGIN_FILLED, LOGOUT_FILLED } from '../constants/auth.action.types'
import { AuthUser } from '../types'

export const setAuth = (payload: AuthUser) => {
  window.localStorage.setItem(APP_ENVIRONMENTS.REACT_APP_JWT_KEY, payload.token)
  window.localStorage.setItem(APP_ENVIRONMENTS.AUTH_KEY, JSON.stringify(omit(payload, ['token'])))
  return {
    type: LOGIN_FILLED,
    payload
  } // return action
}

export const removeAuth = () => {
  window.localStorage.clear()
  return {
    type: LOGOUT_FILLED,
    payload: null
  }
}
