import moment from 'moment'
import { APP_ENVIRONMENTS } from '../configs'

export const getAuth = () => {
  const stored = window.localStorage.getItem(APP_ENVIRONMENTS.AUTH_KEY)
  const token = window.localStorage.getItem(APP_ENVIRONMENTS.REACT_APP_JWT_KEY)
  if (!stored || !token) return null
  const user = JSON.parse(String(window.localStorage.getItem(APP_ENVIRONMENTS.AUTH_KEY)))
  if (!user.token_version || moment().unix() - Number(user.token_version) > APP_ENVIRONMENTS.SECURITY_JWT_TOKEN_EXPIRE) return null
  return user
}

export const getWalletAccount = () => {
  return window.localStorage.getItem(APP_ENVIRONMENTS.WALLET_ACCOUNT_KEY)
}
