import { APP_ENVIRONMENTS } from '../../configs'
import { BaseRequest, OptionsType } from '../BaseRequest'
import { ITournament, ITournaments } from './types'

const { PAGE_INDEX, PAGE_SIZE } = APP_ENVIRONMENTS

export class TournamentRequest extends BaseRequest {
  async getTournaments(
    filters?: any,
    sorts?: { [key: string]: string },
    options: OptionsType = { page_size: PAGE_SIZE, page_index: PAGE_INDEX }
  ): Promise<ITournaments> {
    const url = '/tournaments'
    const data: any = await this.get(url, {
      ...filters,
      ...sorts,
      ...options
    })
    return data
  }

  async createTournament(payload: { id: string; location: string; type: string; class: string; start_time: string }): Promise<ITournament> {
    const url = '/tournaments'
    const data: any = await this.post(url, payload)
    return data
  }

  async getTournamentByKey(key: string): Promise<ITournament> {
    const url = `/tournaments/${key}`
    const data: any = await this.get(url)
    return data
  }

  async updateTournament(
    key: string,
    payload: {
      location: string
      type: string
      class: string
      start_time: string
    }
  ): Promise<ITournament> {
    const url = `/tournaments/${key}`
    const data: any = await this.put(url, payload)
    return data
  }
}
