import { join } from 'lodash'
import { APP_ENVIRONMENTS } from '../../configs'
import { BaseRequest, OptionsType } from '../BaseRequest'
import { IChampion, IChampions } from './types'

const { PAGE_INDEX, PAGE_SIZE } = APP_ENVIRONMENTS

export class ChampionRequest extends BaseRequest {
  async getChampions(
    filters?: { ids?: number[] },
    sorts?: { [key: string]: string },
    options: OptionsType = { page_size: PAGE_SIZE, page_index: PAGE_INDEX }
  ): Promise<IChampions> {
    const url = `/champions`
    const data: any = await this.get(url, {
      ...options,
      ...sorts,
      ...filters,
      ids: join(filters?.ids, ',')
    })
    return data
  }

  async getChampionByKey(key: string): Promise<IChampion> {
    const url = `/champions/${key}`
    const data: any = await this.get(url)
    return data
  }
}
