import React from 'react'
import { useDispatch } from 'react-redux'
import LogoutIcon from '@mui/icons-material/Logout'
import { BottomNavigation, Box, BottomNavigationAction, Paper, Grid } from '@mui/material'
import { removeAuth } from '../../redux/actions/auth.actions'

import './footer.scss'
import { APP_ENVIRONMENTS } from '../../configs'
import { useNavigate } from 'react-router-dom'

export const Footer = (props: any) => {
  const navigate = useNavigate()
  const dispatch = useDispatch()

  const logout = () => {
    dispatch(removeAuth())
    navigate('/')
  }

  return (
    <Box className="footer">
      <Paper sx={{ position: 'fixed', bottom: 0, left: 0, right: 0 }} elevation={3}>
        <Grid container>
          <Grid item sx={{ flex: 1, bgcolor: 'background.paper' }} className="system">
            <div className="wrapper">
              <div className="version">
                Application Version: <span>1.0.0</span>
              </div>
              <div className="endpoint">
                API URL: <span>{APP_ENVIRONMENTS.API_ROOT_URL}</span>
              </div>
            </div>
          </Grid>
          <Grid item xs="auto">
            <BottomNavigation className="footer__navigation">
              <BottomNavigationAction className="logout Mui-selected" showLabel label="Logout" icon={<LogoutIcon />} onClick={logout} />
            </BottomNavigation>
          </Grid>
        </Grid>
      </Paper>
    </Box>
  )
}
