import React, { useEffect, useState } from 'react'
import { Tabs, Tab, Box } from '@mui/material'
import { map, findIndex, filter } from 'lodash'
import { useLocation, useNavigate } from 'react-router-dom'

import './vertical-tabs.scss'
import { Actions, roleCan, Roles } from '../../configs'

type TabProps = {
  userRole: Roles
}

const TABS: Array<{
  key: string
  name: string
  path: string
  require_role_can: Actions
}> = [
  {
    key: 'login_page',
    name: 'Login',
    require_role_can: 'LOGIN',
    path: '/'
  },
  {
    key: 'tournaments_page',
    require_role_can: 'GET_TOURNAMENTS',
    name: 'Tournaments',
    path: '/tournaments'
  },
  {
    key: 'champions_page',
    require_role_can: 'GET_CHAMPIONS',
    name: 'Champions',
    path: '/champions'
  }
]

export const VerticalTabs = (props: TabProps) => {
  const navigate = useNavigate()
  const location = useLocation()
  const [tabIdx, setTabIdx] = useState<number>(-1)
  const { userRole } = props

  const tabs = filter(TABS, page => {
    return roleCan({ role: userRole, action: page.require_role_can })
  })

  useEffect(() => {
    const index = findIndex(tabs, page => {
      return location.pathname === page.path
    })
    setTabIdx(index)
  }, [location, tabs])

  const handleChange = (event: React.SyntheticEvent, newValue: number) => {
    if (tabIdx === newValue || !tabs[newValue]) return
    setTabIdx(newValue)
    navigate(tabs[newValue].path)
  }

  return (
    <Box sx={{ display: 'flex' }} className="box">
      <Tabs
        className={tabs.length > 0 ? 'tabs' : 'tabs none'}
        value={false}
        onChange={handleChange}
        aria-label="Vertical tabs"
        sx={{ borderRight: 1, borderColor: 'divider' }}
      >
        {map(tabs, (page, idx) => (
          <Tab label={page.name} key={idx} value={idx} className={tabIdx === idx ? 'Mui-selected' : ''} />
        ))}
      </Tabs>
    </Box>
  )
}
