import React, { useEffect } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { RootState } from '../../redux'
import { setAccount, setProvider } from '../../redux/actions/wallet.actions'
import { ethers } from 'ethers'
import { AppBar, Toolbar, Typography, Box, Tooltip } from '@mui/material'

import './header.scss'
import { getWalletAccount } from '../../utils/auth'
import { useNavigate } from 'react-router-dom'
import { removeAuth } from '../../redux/actions/auth.actions'
import { VerticalTabs } from '../tabs/VerticalTabs'
import { Roles } from '../../configs'

export const Header = (props: { userRole: Roles }) => {
  const { ethereum } = window
  const navigate = useNavigate()
  const dispatch = useDispatch()
  const walletState = useSelector((state: RootState) => state.wallet)
  const account = walletState.account || getWalletAccount()

  useEffect(() => {
    const logout = () => {
      dispatch(removeAuth())
      navigate('/')
    }
    if (ethereum) {
      ethereum.on('accountsChanged', (accounts: string[]) => {
        if (accounts.length) {
          dispatch(setAccount({ account: accounts[0] }))
          logout()
        } else {
          dispatch(setAccount({ account: null }))
        }
      })

      ethereum.on('chainChanged', (chainId: string) => {
        const provider = new ethers.providers.Web3Provider(ethereum)
        dispatch(setProvider({ provider }))
      })

      ethereum.on('disconnect', () => {
        dispatch(setAccount({ account: null }))
        logout()
      })
    }
  }, [ethereum, dispatch, navigate])

  return (
    <div className="header">
      <AppBar className="app-bar">
        <Toolbar disableGutters className="toolbar">
          <Typography noWrap component="div" sx={{ display: { xs: 'none', md: 'flex' } }}>
            <span className="toolbar__logo">Red Village Admin</span>
          </Typography>
          <Box sx={{ flexGrow: 1, display: { xs: 'none', md: 'flex' } }}>
            <VerticalTabs userRole={props.userRole} />
          </Box>
          <div className="toolbar__top-right">
            {account && (
              <Tooltip title={`Connected to account ${account}`} placement="bottom">
                <div className="account">
                  Authenticated as: {`${account?.substring(0, 12)}...${account?.substring(account.length - 5, account.length)}`}
                </div>
              </Tooltip>
            )}
          </div>
        </Toolbar>
      </AppBar>
    </div>
  )
}
