import React from 'react'
import { Box, Grid, Link, Paper, TextField, Toolbar, Typography } from '@mui/material'
import { ChampionRequest } from '../../requests'
import { useParams } from 'react-router-dom'
import { isEmpty, map } from 'lodash'
import moment from 'moment'
import { TIME_FORMAT } from '../../configs/constants'
import './styles/champion-detail.scss'
import { convertAttributes } from '../../utils/champion'

const championRequest = new ChampionRequest()

interface Champion {
  id: number
  key: string
  name: string
  description: string
  attributes: Array<{
    label: string
    trait: string
  }>
  link_detail?: string
  created: string
  modified: string
}

export const ChampionDetailPage = (props: { readOnly?: boolean }) => {
  const [champion, setChampion] = React.useState<Champion | null>(null)
  const readOnly = props.readOnly || true
  const params = useParams()

  React.useEffect(() => {
    fetchData()
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [])

  const fetchData = async () => {
    try {
      const data = await championRequest.getChampionByKey(String(params.key))
      setChampion({
        key: data.key,
        id: data.id,
        name: data.name,
        description: data.description,
        attributes: convertAttributes(data.attributes),
        created: moment(data.created).format(TIME_FORMAT.YYYY_MMM_DD_HH_MM),
        modified: moment(data.modified).format(TIME_FORMAT.YYYY_MMM_DD_HH_MM)
      })
    } catch (err) {
      console.log(err)
    }
  }

  const commonProps: {
    size: 'small' | 'medium' | undefined
    margin: 'dense' | 'none' | 'normal'
    fullWidth: boolean
    hiddenLabel: boolean
    variant: 'filled' | 'outlined' | 'standard'
    className: string
  } = {
    size: 'small',
    margin: 'normal',
    fullWidth: true,
    hiddenLabel: true,
    variant: 'standard',
    className: 'text'
  }

  return (
    <Box sx={{ bgcolor: 'background.paper' }} className="wrapper-champion-detail">
      <Paper sx={{ width: '100%', mb: 2 }}>
        <Toolbar
          sx={{
            pl: { sm: 2 },
            pr: { xs: 1, sm: 1 }
          }}
        >
          <Typography color="primary" variant="h6" component="div">
            Read Only
          </Typography>
          <Box sx={{ flex: '1' }}></Box>
          <Link
            href={`https://opensea.io/assets/matic/0x4055e3503d1221af4b187cf3b4aa8744332a4d0b/${champion?.id}`}
            sx={{ marginRight: '25px' }}
            target="_blank"
            underline="none"
          >
            View on Opensea
          </Link>
        </Toolbar>
        {!isEmpty(champion) && (
          <Grid container sx={{ bgcolor: 'background.paper' }} className="body">
            <Grid item className="item-field">
              <div className="info">
                <div className="title">
                  <p>Key:</p>
                </div>
                <TextField
                  {...commonProps}
                  id="champion_key"
                  value={champion?.key}
                  InputProps={{
                    readOnly: true
                  }}
                />
              </div>
            </Grid>
            <Grid item className="item-field">
              <div className="info">
                <div className="title">
                  <p>Token ID:</p>
                </div>
                <TextField
                  {...commonProps}
                  id="champion_id"
                  value={champion?.id}
                  InputProps={{
                    readOnly: readOnly
                  }}
                />
              </div>
            </Grid>
            <Grid item className="item-field">
              <div className="info">
                <div className="title">
                  <p>Name:</p>
                </div>
                <TextField
                  {...commonProps}
                  id="champion_name"
                  value={champion?.name}
                  InputProps={{
                    readOnly: readOnly
                  }}
                />
              </div>
            </Grid>
            <Grid item className="item-field">
              <div className="info">
                <div className="title">
                  <p>Description:</p>
                </div>
                <TextField
                  {...commonProps}
                  id="champion_description"
                  value={champion?.description}
                  multiline
                  InputProps={{
                    readOnly: readOnly
                  }}
                />
              </div>
            </Grid>

            {map(champion?.attributes, (el, idx) => {
              return (
                <Grid item className="item-field" key={idx}>
                  <div className="info">
                    <div className="title">
                      <p>{el.label}:</p>
                    </div>
                    <TextField
                      {...commonProps}
                      id={`champion_attributes_${el.label}`}
                      key={`champion_attributes_${el.label}`}
                      value={el.trait}
                      InputProps={{
                        readOnly: readOnly
                      }}
                    />
                  </div>
                </Grid>
              )
            })}
            <Grid item className="item-field">
              <div className="info">
                <div className="title">
                  <p>Created:</p>
                </div>
                <TextField
                  {...commonProps}
                  id="champion_created"
                  value={champion?.created}
                  InputProps={{
                    readOnly: readOnly
                  }}
                />
              </div>
            </Grid>
            <Grid item className="item-field">
              <div className="info">
                <div className="title">
                  <p>Modified:</p>
                </div>
                <TextField
                  {...commonProps}
                  id="champion_modified"
                  value={champion?.modified}
                  InputProps={{
                    readOnly: readOnly
                  }}
                />
              </div>
            </Grid>
          </Grid>
        )}
      </Paper>
    </Box>
  )
}
