import { getAuth } from '../../utils/auth'
import { LOGIN_FILLED, LOGOUT_FILLED } from '../constants/auth.action.types'
import { AuthUserAction, AuthUserReducer, UnAuthUserAction } from '../types'

type ActionMixed = AuthUserAction | UnAuthUserAction
type MixinReducers = AuthUserReducer

export const auth = (state: any, action: ActionMixed): MixinReducers => {
  state = state || {}
  switch (action.type) {
    case LOGIN_FILLED:
      return {
        ...state,
        user: action.payload
      }
    case LOGOUT_FILLED:
      return {
        ...state,
        user: null
      }
    default:
      return {
        ...state,
        user: state.user || getAuth()
      }
  }
}
