import React from 'react'
import { TournamentRequest } from '../../requests'
import { DataListTable } from '../../components/commons/DataTable'
import './styles/tournament.scss'
import { find, map } from 'lodash'
import moment from 'moment'
import { TIME_FORMAT, TOURNAMENT_CLASSES } from '../../configs/constants'
import { useNavigate } from 'react-router-dom'
import { ITournaments } from '../../requests/tournaments/types'
import { Button, TextField, Toolbar, Tooltip, Typography } from '@mui/material'
import AdapterMoment from '@mui/lab/AdapterMoment'
import { DateTimePicker, LocalizationProvider } from '@mui/lab'

const tournamentRequest = new TournamentRequest()

type Order = 'asc' | 'desc'

interface Tournament {
  index: number
  id: string
  class: string
  location: string
  start_time: string
  type: string
  key: string
  link_detail?: string
  link_edit?: string
}

interface HeadCell {
  id: keyof Tournament
  label: string
  can_sort: boolean
}

// apply to props
const headCells: HeadCell[] = [
  {
    id: 'id',
    label: 'ID',
    can_sort: true
  },
  {
    id: 'class',
    label: 'Class',
    can_sort: true
  },
  {
    id: 'location',
    label: 'Location',
    can_sort: true
  },
  {
    id: 'start_time',
    label: 'Start Time',
    can_sort: true
  },
  {
    id: 'type',
    label: 'Type',
    can_sort: true
  }
]

export const TournamentsPage = (props: any) => {
  const navigate = useNavigate()
  const [tournaments, setTournaments] = React.useState<Tournament[]>([])
  const [pageSize, setPageSize] = React.useState<number>(30)
  const [pageInfo, setPageInfo] = React.useState<
    | {
        current_page: number
        total_pages: number
        item_count: number
        total_count: number
      }
    | undefined
  >()

  const [timeFrom, setTimeFrom] = React.useState<string | null>(null)
  const [timeTo, setTimeTo] = React.useState<string | null>(null)

  const mapFilterQuery = () => {
    return {
      filter_time_from: timeFrom || '',
      filter_time_to: timeTo || ''
    }
  }

  const mapData = (data: ITournaments) => {
    const rows = map(data.items, (el, idx) => {
      return {
        index: idx,
        key: el.key,
        id: el.id,
        class: String(
          find(TOURNAMENT_CLASSES, v => {
            return v.value === el.class
          })?.label
        ),
        location: el.location,
        start_time: el.start_time,
        type: el.type,
        link_detail: `/tournaments/${el.key}/detail`,
        link_edit: `/tournaments/${el.key}/edit`
      }
    })
    setPageInfo({
      current_page: data.current_page,
      item_count: data.item_count,
      total_count: data.total_count,
      total_pages: data.total_pages
    })
    setTournaments(rows)
  }

  const fetchData = async () => {
    try {
      const data = await tournamentRequest.getTournaments()
      mapData(data)
    } catch {}
  }

  React.useEffect(() => {
    fetchData()
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [])

  const onCreate = () => {
    navigate('/tournaments/add')
  }

  const onPageSizeChange = async (size: number, orderBy: keyof Tournament, order: Order) => {
    const sorts =
      orderBy !== 'index'
        ? {
            [`sort_${orderBy}`]: order
          }
        : {}
    try {
      const data = await tournamentRequest.getTournaments(
        {
          ...mapFilterQuery()
        },
        sorts,
        {
          page_index: 0,
          page_size: size
        }
      )
      mapData(data)
      setPageSize(size)
    } catch (err) {}
  }

  const onPageChange = async (page: number, size: number, orderBy: keyof Tournament, order: Order) => {
    const sorts =
      orderBy !== 'index'
        ? {
            [`sort_${orderBy}`]: order
          }
        : {}
    try {
      const data = await tournamentRequest.getTournaments(
        {
          ...mapFilterQuery()
        },
        sorts,
        {
          page_index: page,
          page_size: size
        }
      )
      mapData(data)
    } catch (err) {}
  }

  const onFieldSort = async (property: keyof Tournament, order: 'asc' | 'desc', size: number) => {
    try {
      const data = await tournamentRequest.getTournaments(
        {
          ...mapFilterQuery()
        },
        {
          [`sort_${property}`]: order
        },
        {
          page_index: 0,
          page_size: size
        }
      )
      mapData(data)
    } catch (err) {}
  }

  const onFilter = async () => {
    try {
      const data = await tournamentRequest.getTournaments(
        {
          ...mapFilterQuery()
        },
        {},
        {
          page_index: 0,
          page_size: pageSize
        }
      )
      mapData(data)
    } catch (err) {}
  }

  return (
    <div className="wrapper-tournaments">
      <Toolbar
        sx={{
          pl: { sm: 2 },
          pr: { xs: 1, sm: 1 },
          marginBottom: 3
        }}
      >
        <span className="filter-name">Start Time</span>
        <LocalizationProvider dateAdapter={AdapterMoment}>
          <DateTimePicker
            clearable
            ampm={false}
            renderInput={dataProps => <TextField {...dataProps} />}
            label="From"
            value={timeFrom}
            onChange={newValue => {
              setTimeFrom(newValue ? moment(newValue).format(TIME_FORMAT.YYYY_MM_DD_HH_MM) : newValue)
            }}
            inputFormat={TIME_FORMAT.INPUT_FORMAT}
          />
        </LocalizationProvider>
        <LocalizationProvider dateAdapter={AdapterMoment}>
          <DateTimePicker
            clearable
            ampm={false}
            renderInput={dataProps => <TextField sx={{ marginLeft: 2 }} {...dataProps} />}
            label="To"
            value={timeTo}
            onChange={newValue => {
              setTimeTo(newValue ? moment(newValue).format(TIME_FORMAT.YYYY_MM_DD_HH_MM) : newValue)
            }}
            inputFormat={TIME_FORMAT.INPUT_FORMAT}
          />
        </LocalizationProvider>
        <Typography sx={{ flex: 1 }}></Typography>
        <Tooltip title="Filter tournaments">
          <Button size="large" onClick={onFilter}>
            Apply Filter
          </Button>
        </Tooltip>
      </Toolbar>
      <DataListTable
        toolbar={{
          title: 'Tournaments',
          // remove: {
          //   onDelete
          // },
          create: {
            onCreate
          }
        }}
        rows={tournaments}
        heads={headCells}
        filters={{
          onRowsPerPageChange: onPageSizeChange,
          onPageChange,
          onFieldSort
        }}
        page={pageInfo}
      />
    </div>
  )
}
