import React from 'react'
import { Box, Button, FormControl, Grid, MenuItem, Paper, Select, TextField, Toolbar, Typography } from '@mui/material'
import { map } from 'lodash'
import { TournamentRequest } from '../../requests'
import { LoadingPage } from '../../components/commons/LoadingPage'
import { useSnackbar } from 'notistack'
import AdapterMoment from '@mui/lab/AdapterMoment'
import { DateTimePicker, LocalizationProvider } from '@mui/lab'

import './styles/tournament-add.scss'
import moment from 'moment'
import { TIME_FORMAT, TOURNAMENT_CLASSES } from '../../configs/constants'
import { useNavigate } from 'react-router-dom'
import { connectTournamentContract } from '../../utils/ethereum'
import { ethers } from 'ethers'

const tournamentRequest = new TournamentRequest()

export const TournamentAdd = (props: any) => {
  const navigate = useNavigate()
  const [id, setId] = React.useState<string>('')
  const [location, setLocation] = React.useState<string>('')
  const [type] = React.useState<string>('tournament')
  const [startTime, setStartTime] = React.useState<string | null>(moment().format(TIME_FORMAT.YYYY_MM_DD_HH_MM))
  const [tClass, setTClass] = React.useState<string>('blooding')
  const [loading, setLoading] = React.useState<boolean>(false)
  const { enqueueSnackbar } = useSnackbar()
  const [timer, setTimer] = React.useState<any>()

  const [fee, setFee] = React.useState<string>('--')
  const [size, setSize] = React.useState<string>('--')

  const fetchOnchainData = async (tournamentId: string) => {
    if (tournamentId === '' || isNaN(Number(tournamentId))) return
    const contract = connectTournamentContract()
    const requests = await Promise.allSettled([
      contract.getFee(ethers.BigNumber.from(tournamentId)),
      contract.getSize(ethers.BigNumber.from(tournamentId))
    ])
    if (requests[0].status === 'fulfilled') {
      setFee(`${ethers.utils.formatEther(requests[0].value)} ETH`)
    } else {
      setFee('invalid')
    }
    if (requests[1].status === 'fulfilled') {
      setSize(Number(requests[1].value).toString())
    } else {
      setSize('invalid')
    }
  }

  const commonProps: {
    size: 'small' | 'medium' | undefined
    margin: 'dense' | 'none' | 'normal'
    fullWidth: boolean
    hiddenLabel: boolean
    variant: 'filled' | 'outlined' | 'standard'
    multiline: boolean
  } = {
    size: 'medium',
    margin: 'none',
    fullWidth: true,
    hiddenLabel: true,
    variant: 'outlined',
    multiline: true
  }

  const fields: {
    size: 'small' | 'medium' | undefined
    margin: 'dense' | 'none' | 'normal'
    fullWidth: boolean
    hiddenLabel: boolean
    variant: 'filled' | 'outlined' | 'standard'
    label_field: string
    label?: string
    id: string
    key: string
    value: string | number | undefined
    onChange: (event: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement>) => void
    helperText?: string
  }[] = [
    {
      ...commonProps,
      label_field: 'Tournament ID',
      id: 'tournament_id',
      key: 'tournament_id',
      value: id,
      onChange: event => {
        setId(event.target.value)
        clearTimeout(timer)
        setTimer(
          setTimeout(() => {
            fetchOnchainData(event.target.value)
          }, 1000)
        )
      }
    },
    {
      ...commonProps,
      label_field: 'Location',
      id: 'tournament_location',
      key: 'tournament_location',
      value: location,
      onChange: event => {
        setLocation(event.target.value)
      }
    }
  ]

  const onCreateTournament = async () => {
    try {
      setLoading(true)
      const res = await tournamentRequest.createTournament({
        id,
        location,
        type,
        start_time: startTime
          ? moment(startTime, TIME_FORMAT.YYYY_MM_DD_HH_MM).utcOffset(TIME_FORMAT.UTC_OFFSET).format(TIME_FORMAT.YYYY_MM_DD_HH_MM)
          : '',
        class: tClass
      })
      setLoading(false)
      navigate(`/tournaments/${res.key}/detail`)
    } catch (err: any) {
      if (err?.http_status_code === 422) {
        const message = map(err?.errors, (value, key) => {
          return `${key}: ${value?.message}`
        })[0]
        enqueueSnackbar(message, {
          variant: 'info'
        })
      } else {
        enqueueSnackbar(err?.message, {
          variant: 'info'
        })
      }
      setLoading(false)
    }
  }

  return (
    <Box sx={{ bgcolor: 'background.paper' }} className="wrapper-tournament-add">
      <Paper sx={{ width: '100%', mb: 2 }}>
        <Toolbar
          sx={{
            pl: { sm: 2 },
            pr: { xs: 1, sm: 1 }
          }}
        >
          <Typography color="primary" variant="h6" component="div">
            Create a new tournament
          </Typography>
        </Toolbar>
        <Grid container sx={{ bgcolor: 'background.paper', marginTop: '5px' }} className="body" spacing={2}>
          {map(fields, (el, idx) => {
            return (
              <Grid item className="item-field" key={idx} xs={12}>
                <div className="info">
                  <div className="label">{el.label_field} :</div>
                  <FormControl fullWidth>
                    <TextField {...el} hiddenLabel />
                  </FormControl>
                </div>
              </Grid>
            )
          })}
          <Grid item className="item-field" key="tournament_class" xs={12}>
            <div className="info">
              <div className="label">Class :</div>
              <FormControl fullWidth>
                <Select
                  labelId="tournament_class"
                  onChange={event => {
                    setTClass(event.target.value)
                  }}
                  margin="none"
                  {...{
                    id: 'tournament_class',
                    key: 'tournament_class',
                    value: tClass
                  }}
                >
                  {map(TOURNAMENT_CLASSES, (el, k) => {
                    return (
                      <MenuItem key={k} value={el.value}>
                        {el.label}
                      </MenuItem>
                    )
                  })}
                </Select>
              </FormControl>
            </div>
          </Grid>
          <Grid item className="item-field" key="tournament_start_time" xs={12}>
            <div className="info">
              <div className="label">Tournament Start Time :</div>
              <FormControl fullWidth>
                <LocalizationProvider dateAdapter={AdapterMoment}>
                  <DateTimePicker
                    renderInput={dataProps => (
                      <TextField
                        {...dataProps}
                        value={moment(startTime).format(TIME_FORMAT.YYYY_MMM_DD_HH_MM)}
                        helperText="Time will be stored in GMT format"
                      />
                    )}
                    value={startTime}
                    onChange={newValue => {
                      setStartTime(moment(newValue).format(TIME_FORMAT.YYYY_MM_DD_HH_MM))
                    }}
                    inputFormat={TIME_FORMAT.INPUT_FORMAT}
                    // minDateTime={momentUtc}
                  />
                </LocalizationProvider>
              </FormControl>
            </div>
          </Grid>
          <Grid container className="item-field" justifyContent="flex-end" alignItems="center" sx={{ marginTop: '20px' }}>
            <Button variant="contained" onClick={onCreateTournament}>
              Create
            </Button>
          </Grid>
        </Grid>
      </Paper>
      <Paper sx={{ width: '100%', mb: 2 }}>
        <Toolbar
          sx={{
            pl: { sm: 2 },
            pr: { xs: 1, sm: 1 }
          }}
        >
          <Typography color="primary" variant="h6" component="div">
            Smart Contract Data
          </Typography>
        </Toolbar>
        <Grid container sx={{ bgcolor: 'background.paper' }} className="body-contract">
          <Grid item className="item-field" key="smart_contract_fee">
            <div className="info">
              <div className="title">
                <p>Fee: &nbsp;</p>
              </div>
              <Typography color="primary" variant="subtitle1" component="div" sx={{ marginTop: 'auto', marginBottom: 'auto' }}>
                {fee}
              </Typography>
            </div>
          </Grid>
          <Grid item className="item-field" key="smart_contract_size">
            <div className="info">
              <div className="title">
                <p>Size: &nbsp;</p>
              </div>
              <Typography color="primary" variant="subtitle1" component="div" sx={{ marginTop: 'auto', marginBottom: 'auto' }}>
                {size}
              </Typography>
            </div>
          </Grid>
        </Grid>
      </Paper>
      <LoadingPage open={loading} onClose={() => {}} />
    </Box>
  )
}
