import { find, map } from 'lodash'

const BACKGROUNDS = ['Black']
const BLOODLINES = ['Genesis']
const GENOTYPES = ['R1', 'R2', 'R3']
const CHARACTER_CLASSES = ['Druid', 'Wizard', 'Barbarian', 'Ranger', 'Paladin']
const BREEDS = ['Undead', 'Orc', 'Half Dwarf', 'Origin', 'High Born', 'Nordic', 'Elf']
const ARMOUR_COLORS = [
  'Snowfall',
  'Meadowlight',
  'Rune',
  'Emerald',
  'Stategreen',
  'Forestdeep',
  'Frost',
  'Coldshade',
  'Rainstorm',
  'Thundercloud',
  'Gloombane',
  'Splendid',
  'Memorial',
  'Winterkill',
  'Winterstorm',
  'Spectral',
  'Thunderstorm',
  'Charcoal',
  'Chaos',
  'Onyx',
  'Royal Black',
  'Dark of Night',
  'Royal White',
  'Charlemagne',
  'Ivory',
  'White Quartz',
  'Dynasty',
  'Mercia',
  'Benedictine',
  'Bone',
  'Alpensun',
  'Coronet',
  'Candlelight',
  'Duskglow',
  'Ochrewash',
  'Firefly',
  'Polarwind',
  'Pennyroyal',
  'Mayhem',
  'Greatwood',
  'Blood',
  'Brimstone',
  'Crime-of-Passion',
  'Red-of-War',
  'Ruby Red',
  'Sovereign',
  'Gloom'
]
const HAIR_COLORS = ['None', 'Tan', 'Blonde', 'Black', 'Gray', 'Brown', 'Auburn']
const HAIR_CLASSES = ['Druid', 'Wizard', 'Barbarian', 'Ranger', 'Paladin']
const HAIR_STYLES = ['Braid', 'Short', 'Wartail', 'Mohawk', 'Long', 'Bald', 'Hightail', 'Sidepart']
const WARPAINT_COLORS = ['None', 'Black', 'White', 'Blue', 'Yellow', 'Green', 'Silver', 'Red', 'Gold']
const WARPAINT_STYLES = ['None', 'Scar', 'Ragnarok', 'Trident', 'Rogue', 'Vortex', 'Sceptre', 'Hex', 'Mimic', 'Sigil', 'Crest']

const MAPPING = {
  background: {
    value: BACKGROUNDS,
    label: 'Background'
  },
  bloodline: {
    value: BLOODLINES,
    label: 'Bloodline'
  },
  genotype: {
    value: GENOTYPES,
    label: 'Genotype'
  },
  armour_color: {
    value: ARMOUR_COLORS,
    label: 'Armour Color'
  },
  character_class: {
    value: CHARACTER_CLASSES,
    label: 'Character Class'
  },
  breed: {
    value: BREEDS,
    label: 'Breed'
  }
}

export const convertAttributes = (
  attrs: Array<{
    trait_type: string
    value: number
  }>
): {
  label: 'Background' | 'Bloodline' | 'Genotype' | 'Armour Color' | 'Character Class' | 'Breed'
  trait: string
}[] => {
  const attributes = map(MAPPING, (value, key) => {
    const attr = find(attrs, attr => {
      return attr.trait_type === key
    })
    return {
      label: value.label,
      trait: value.value[Number(attr?.value)]
    }
  })
  const hairColorIdx = Number(
    find(attrs, attr => {
      return attr.trait_type === 'hair_color'
    })?.value
  )
  const hairClassIdx = Number(
    find(attrs, attr => {
      return attr.trait_type === 'hair_class'
    })?.value
  )
  const hairStyleIdx = Number(
    find(attrs, attr => {
      return attr.trait_type === 'hair_style'
    })?.value
  )
  attributes.push({
    label: 'Hair',
    trait: `${hairColorIdx > 0 ? HAIR_COLORS[hairColorIdx] : ''} ${HAIR_CLASSES[hairClassIdx]} ${HAIR_STYLES[hairStyleIdx]}`.trim()
  })

  const warpaintColorIdx = Number(
    find(attrs, attr => {
      return attr.trait_type === 'warpaint_color'
    })?.value
  )
  const warpaintStyleIdx = Number(
    find(attrs, attr => {
      return attr.trait_type === 'warpaint_style'
    })?.value
  )
  attributes.push({
    label: 'Warpaint',
    trait:
      `${warpaintColorIdx > 0 ? WARPAINT_COLORS[warpaintColorIdx] : ''} ${warpaintStyleIdx > 0 ? WARPAINT_STYLES[hairClassIdx] : 'None'}`.trim() ||
      'None'
  })
  return attributes as {
    label: 'Background' | 'Bloodline' | 'Genotype' | 'Armour Color' | 'Character Class' | 'Breed'
    trait: string
  }[]
}
