import { BaseRequest } from '../BaseRequest'
import { ILogin, IWalletConnect } from './types'

export class AuthRequest extends BaseRequest {
  async connectWallet(payload: IWalletConnect): Promise<{ nonce_text: string }> {
    const url = '/users/wallet/connect'
    const data: { nonce_text: string } = await this.post(url, payload)
    return data
  }

  async login(payload: ILogin): Promise<any> {
    const url = '/users/wallet/login'
    const data = await this.post(url, payload)
    return data
  }
}
