import React from 'react'
import { Box, Button, Grid, Paper, Toolbar, Typography } from '@mui/material'
import { useNavigate, useParams } from 'react-router-dom'
import { find, isEmpty, map } from 'lodash'
import './styles/tournament-detail.scss'
import { TournamentRequest } from '../../requests'
import moment from 'moment'
import { TIME_FORMAT, TOURNAMENT_CLASSES } from '../../configs/constants'
import { connectTournamentContract } from '../../utils/ethereum'
import { ethers } from 'ethers'

const tournamentRequest = new TournamentRequest()

interface Tournament {
  key: {
    label: string
    value: string
  }
  id: {
    label: string
    value: string
  }
  class: {
    label: string
    value: string
  }
  location: {
    label: string
    value: string
  }
  start_time: {
    label: string
    value: string
  }
  type: {
    label: string
    value: string
  }
  created: {
    label: string
    value: string
  }
  modified: {
    label: string
    value: string
  }
}

export const TournamentDetailPage = (props: any) => {
  const navigate = useNavigate()
  const [id, setId] = React.useState<string>('')
  const [tournament, setTournament] = React.useState<Tournament | undefined>(undefined)
  const params = useParams()

  const [fee, setFee] = React.useState<string>('--')
  const [size, setSize] = React.useState<string>('--')

  React.useEffect(() => {
    fetchOnchainData()
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [id])

  const fetchOnchainData = async () => {
    if (id === '' || isNaN(Number(id))) return
    const contract = connectTournamentContract()
    const requests = await Promise.allSettled([contract.getFee(ethers.BigNumber.from(id)), contract.getSize(ethers.BigNumber.from(id))])
    if (requests[0].status === 'fulfilled') {
      setFee(`${ethers.utils.formatEther(requests[0].value)} ETH`)
    } else {
      setFee('invalid')
    }
    if (requests[1].status === 'fulfilled') {
      setSize(Number(requests[1].value).toString())
    } else {
      setSize('invalid')
    }
  }

  React.useEffect(() => {
    fetchData()
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [])

  const fetchData = async () => {
    try {
      const data = await tournamentRequest.getTournamentByKey(String(params.key))
      setId(String(data.id))
      setTournament({
        key: {
          label: 'Key',
          value: data.key
        },
        id: {
          label: 'Tournament ID',
          value: data.id
        },
        class: {
          label: 'Class',
          value: String(find(TOURNAMENT_CLASSES, el => el.value === data.class)?.label)
        },
        location: {
          label: 'Location',
          value: data.location
        },
        start_time: {
          label: 'Start Time',
          value: data.start_time + '  (GMT)'
        },
        type: {
          label: 'Type',
          value: data.type
        },
        created: {
          label: 'Created',
          value: moment(data.created).format(TIME_FORMAT.YYYY_MM_DD_HH_MM)
        },
        modified: {
          label: 'Modified',
          value: moment(data.modified).format(TIME_FORMAT.YYYY_MM_DD_HH_MM)
        }
      })
    } catch (err) {
      console.log(err)
    }
  }
  return (
    <Box sx={{ bgcolor: 'background.paper' }} className="wrapper-tournament-detail">
      <Paper sx={{ width: '100%', mb: 2 }}>
        <Toolbar
          sx={{
            pl: { sm: 2 },
            pr: { xs: 1, sm: 1 }
          }}
        >
          Tournament &nbsp;
          <Typography color="primary" variant="h6" component="div">
            {String(params.key)}
          </Typography>
          <Typography sx={{ flex: 1 }}></Typography>
          <Button
            onClick={() => {
              navigate(`/tournaments/${params.key}/edit`)
            }}
          >
            Edit
          </Button>
        </Toolbar>
        {!isEmpty(tournament) && (
          <Grid container sx={{ bgcolor: 'background.paper' }} className="body">
            {map(tournament, (value, key) => {
              return (
                <Grid item className="item-field" key={key}>
                  <div className="info">
                    <div className="title">
                      <p>{value.label}: &nbsp;</p>
                    </div>
                    <Typography
                      color="primary"
                      id={key}
                      key={key}
                      sx={{ marginTop: 'auto', marginBottom: 'auto' }}
                      variant="subtitle1"
                      component="div"
                    >
                      {value.value}
                    </Typography>
                  </div>
                </Grid>
              )
            })}
          </Grid>
        )}
      </Paper>
      <Paper sx={{ width: '100%', mb: 2 }}>
        <Toolbar
          sx={{
            pl: { sm: 2 },
            pr: { xs: 1, sm: 1 }
          }}
        >
          <Typography color="primary" variant="h6" component="div">
            Smart Contract Data
          </Typography>
        </Toolbar>
        <Grid container sx={{ bgcolor: 'background.paper' }} className="body">
          <Grid item className="item-field" key="smart_contract_fee">
            <div className="info">
              <div className="title">
                <p>Fee: &nbsp;</p>
              </div>
              <Typography color="primary" variant="subtitle1" component="div" sx={{ marginTop: 'auto', marginBottom: 'auto' }}>
                {fee}
              </Typography>
            </div>
          </Grid>
          <Grid item className="item-field" key="smart_contract_size">
            <div className="info">
              <div className="title">
                <p>Size: &nbsp;</p>
              </div>
              <Typography color="primary" variant="subtitle1" component="div" sx={{ marginTop: 'auto', marginBottom: 'auto' }}>
                {size}
              </Typography>
            </div>
          </Grid>
        </Grid>
      </Paper>
    </Box>
  )
}
