import React, { useState } from 'react'
import { Button, Container } from '@mui/material'
import { DialogCustom } from '../../components/commons/Dialog'
import { useSelector, useDispatch } from 'react-redux'
import { RootState } from '../../redux'
import { setAccount, setProvider } from '../../redux/actions/wallet.actions'
import { ethers } from 'ethers'
import { ErrorKey, ERRORS } from '../../errors'
import { RequestFactory, AuthRequest } from '../../requests'

import './login.scss'
import { setAuth } from '../../redux/actions/auth.actions'
import moment from 'moment'

const authRequest = RequestFactory.getRequest<AuthRequest>('AuthRequest')

export const LoginPage = (props: any) => {
  const { ethereum } = window
  const [title, setTitle] = useState<string>('')
  const [description, setDescription] = useState<string>('')
  const [openDialog, setOpenDialog] = useState<boolean>(false)
  const dispatch = useDispatch()
  const walletState = useSelector((state: RootState) => state.wallet)
  const provider = walletState.provider
  const account = walletState.account

  const showPopup = (key: ErrorKey) => {
    setTitle(ERRORS[key].title)
    setDescription(ERRORS[key].description)
    setOpenDialog(true)
  }

  const connectWallet = async () => {
    if (provider) return true
    if (ethereum) {
      try {
        await ethereum.request({ method: 'eth_requestAccounts' })
        const newProvider = new ethers.providers.Web3Provider(ethereum)
        const { chainId } = await newProvider.getNetwork()
        if (!isMainnet(chainId)) {
          return false
        }
        await setWalletState(newProvider)
      } catch {
        showPopup('METAMASK_NOT_CONNECTED')
        return false
      }
      return true
    }
    showPopup('METAMASK_NOT_CONNECTED')
    return false
  }

  const setWalletState = async (ethProvider: ethers.providers.Web3Provider) => {
    dispatch(setProvider({ provider: ethProvider }))
    if (!walletState.account) {
      const ethAccount = await ethProvider.getSigner().getAddress()
      dispatch(setAccount({ account: ethAccount }))
    }
  }

  const isMainnet = (chainId: number): boolean => {
    if (chainId === 0) {
      // chainId !== 1
      dispatch(setProvider({ provider: null }))
      dispatch(setAccount({ account: null }))
      showPopup('MAINNET_ERROR_CONNECTED')
      return false
    }
    return true
  }

  const login = async () => {
    if (!provider || !account) return
    const { nonce_text } = await authRequest.connectWallet({ wallet_address: String(account) })
    const signer = provider?.getSigner()
    const signature = await signer?.signMessage(nonce_text)
    const data = await authRequest.login({ wallet_address: String(account), signature: String(signature) })
    dispatch(
      setAuth({
        role: data.role,
        token: data.token,
        username: data.username,
        wallet_address: data.wallet_address,
        token_version: moment().unix(),
        first_name: data.first_name,
        last_name: data.last_name
      })
    )
  }

  return (
    <Container className="container">
      <section className="body_section">
        {!account && (
          <Button color="primary" variant="contained" className="login-form" onClick={connectWallet} size="large">
            Connect Wallet
          </Button>
        )}
        {account && (
          <Button color="primary" variant="contained" className="login-form" onClick={login} size="large">
            Log In
          </Button>
        )}
        <div>Please connect using your metamask wallet then log in</div>
      </section>

      <DialogCustom
        cancel_button_name="Dismiss"
        title={title}
        description={description}
        open={openDialog}
        handle_close={() => setOpenDialog(false)}
      />
    </Container>
  )
}
