import { applyMiddleware, combineReducers, createStore } from 'redux'
import { wallet } from './reducer/wallet.reducer'
import { auth } from './reducer/auth.reducer'
import { APP_ENVIRONMENTS } from '../configs'
import { authMiddleware, crashReporter, logger } from './middleware'

export const rootReducer = combineReducers({
  wallet,
  auth
})

export type RootState = ReturnType<typeof rootReducer>

const storeMiddleware = APP_ENVIRONMENTS.IS_PRODUCTION ? applyMiddleware(authMiddleware) : applyMiddleware(authMiddleware, logger, crashReporter)

export const store = createStore(rootReducer, storeMiddleware)
